







































import { Component, Vue, Prop } from "vue-property-decorator";
// import { Form as ElForm } from "element-ui";
import { getRoleList } from "../../api/system/index2";
// import { DictionariesModule } from "../../store/modules/dictionaries";
@Component({
  components: {},
})
export default class JobAddEdit extends Vue {
  constructor() {
    super();
  }
  @Prop({ default: null }) data: any;
  dialogVisible = true;

  generateData() {
    const dataList: any = [];
    const cities = ["上海", "北京", "广州", "深圳", "南京", "西安", "成都"];
    const pinyin = [
      "shanghai",
      "beijing",
      "guangzhou",
      "shenzhen",
      "nanjing",
      "xian",
      "chengdu",
    ];
    cities.forEach((city, index) => {
      dataList.push({
        label: city,
        key: index,
        pinyin: pinyin[index],
      });
    });
    return dataList;
  }
  leftData: any = [];
  rightData: any = [];

  filterMethod(query: any, item: any) {
    return item.code.indexOf(query) > -1 || item.name.indexOf(query) > -1;
  }

  cancel() {
    this.$emit("cancel", false);
  }

  finish() {
    this.$emit("finish", {});
  }

  async created() {
    console.log(this.data);
    const { total, list } = await getRoleList();
    list.forEach((item: any) => {
      item.key = item.id;
      item.label = item.name;
    });
    console.log(total, list);
    this.leftData = list;
  }
}
